import originJsonp from 'jsonp'
import { parseParamUrl } from '@/common/helpers/utils.js'

// res.code
export const API_OK = 0
// message
export const API_ERROR = 'API_ERROR'

// 
export const commonParams = {
  g_tk: 1928093487,
  inCharset: 'utf-8',
  outCharset: 'utf-8',
  notice: 0,
  format: 'jsonp'
}

// jsonp
export const options = {
  param: 'jsonpCallback',
  // 1s
  timeout: 1000
}

// jsonpaxios
export function jsonp({url = '', data = {}, options = {param: 'jsonpCallback', timeout: 1000}} = {}) {
  url = parseParamUrl(url, data)

  return new Promise((resolve, reject) => {
    originJsonp(url, options, (err, data) => {
      if (!err) {
        resolve(data)
      } else {
        reject(API_ERROR)
      }
    })
  })
}

// apiHandlerajaxPromisecatch
// ajax,
export function apiHandler(callback) {
  return new Promise((resolve, reject) => {
    callback().then((data) => {
      resolve(data)
    }).catch((message) => {
      console.log(message)
      switch (message) {
        case API_ERROR:
          console.log('')
          break
        default:
          console.log('')
          break
      }
    })
  })
}



