<template>
  <div class="vi-slide-item">
    <slot>
      <a :href="item.linkUrl">
        <img :src="item.picUrl" @load="loadImage">
      </a>
    </slot>
  </div>
</template>

<script>
const COMPONENT_NAME = 'vi-slide-item'

const EVENT_LOAD_IMAGE = 'load-image'

export default {
  name: 'vi-slide-item',
  props: {
    item: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    loadImage() {
      this.$emit(EVENT_LOAD_IMAGE)
    },
  }
}
</script>

<style lang="stylus">
.vi-slide-item
  float: left
  a
    display: block
    overflow: hidden
    text-decoration: none
    img
      // img
      display: block
      // img width100%div(float)
      // floatimg
      // widthheight,float
      // slide-wrapperimg
      width: 100%
</style>



