<template>
  <div :class="$style['tab']">
    <div class="tab-item"
      :class="{'active': item.router.name === $route.name}"
      v-for="(item, index) in tabList"
      :key="index"
      @touchstart="pageTurn(index)">
      <span class="tab-link">{{item.title}}</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabList: [
        {
          title: '',
          router: {
            name: 'IndexRecommend',
          },
        },
        {
          title: '',
          router: {
            name: 'IndexSinger',
          },
        },
        {
          title: '',
          router: {
            name: 'IndexRank',
          },
        },
        {
          title: '',
          router: {
            name: 'IndexSearch',
          },
        },
      ],
    }
  },
  methods: {
    pageTurn(index) {
      this.$router.push(this.tabList[index].router)
    }
  }
}
</script>

<style lang="stylus" module>
@import "~@/common/stylus/var/font-size.styl"
@import "~@/common/stylus/var/color.styl"

.tab
  display: flex
  height: 44px
  // background: peru
  // line-height,
  // ,
  // span,ainline-block,
  // span,ainline-block,
  line-height: 44px
  font-size: $font-size-medium
  background: $color-background
  :global
    .tab-item
      flex: 1
      text-align: center
      .tab-link
        padding: 0px 5px 5px 5px
        color: $color-text-l
        // background: gold
      &.active
        .tab-link
          color: $color-theme
          border-bottom: 2px solid $color-theme
</style>



