import {
  commonParams,
  API_OK,
  API_ERROR,
  apiHandler,
} from './config'

import { deepClone } from '@/common/helpers/utils.js'

import axios from 'axios'

const baseURL = process.env.baseURL

const instance = axios.create({
  baseURL: baseURL,
  validateStatus(status) {
    // status200~300Promise.resolve(res)
    // return status >= 200 && status < 300
    // statusCode,code
    // instance.interceptors.response
    return true
  }
})

// ,404code
instance.interceptors.response.use((response) => {
  // if (response.request.responseURL.indexOf('lyric/fcgi-bin/fcg_query_lyric_new.fcg') !== -1) {
  //   /* eslint no-useless-escape: "off" */
  //   response.data = response.data.replace(/^\w+\((.*)\)$/g, '$1')
  //   response.data = JSON.parse(response.data)
  // }
  // code
  if ((response.status === 200) && response.data.code === API_OK) {
    return Promise.resolve(response)
  } else {
    return Promise.reject(API_ERROR)
  }
})

export function getLyric(params) {
  const url = 'lyric/fcgi-bin/fcg_query_lyric_new.fcg'
  const data = deepClone({}, commonParams, params)
  return apiHandler(() => {
    return instance({
      method: 'get',
      url: url,
      params: data
    }).then((res) => {
      return Promise.resolve(res.data)
    })
  })
}



